body {
  background-image: url("bckg.ed5d6b17.jpg");
  background-repeat: repeat;
}

.text {
  background-color: #fff;
  border: 5px solid #daa520;
  border-radius: 20px;
  padding: 8px;
  font-family: Rubik, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.6;
}

.main-choice {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.main-choice a {
  min-width: 500px;
  height: 333px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-clip: content-box;
  border: 5px solid #daa520;
  border-radius: 20px;
  transition: transform .2s ease-in-out, border-color .2s ease-in-out;
  transform: scale(1);
}

.main-choice a.cat {
  background-image: url("cat.daaa6732.jpg");
}

.main-choice a.dog {
  background-image: url("dog.9def4028.jpg");
}

.main-choice a:hover {
  transform: scale(1.05);
}

.main-choice a.cat:hover {
  border-color: red;
}

.main-choice a.dog:hover {
  border-color: #228b22;
}

.header {
  width: 900px;
  text-align: center;
  margin: 50px auto;
  padding: 20px;
  font-size: 60px;
  font-weight: 500;
  line-height: 1;
}

.selector {
  display: inline-block;
}

.label-select {
  border: none;
  font-size: 25px;
}

.breed-select {
  width: 360px;
  border-radius: 15px;
  margin-left: 10px;
  padding-right: 10px;
  box-shadow: inset 0 0 5px 1px rgba(0, 0, 0, .5);
}

.breed-select:focus {
  outline: none;
}

.header.choice {
  width: fit-content;
}

.temperament-header {
  font-weight: 700;
}

.breed-info {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
}

.breed-info-right {
  padding: 0 20px;
}

.breed-name {
  margin: 0;
  font-size: 50px;
  font-weight: 500;
  line-height: 1;
}

.animal-img {
  object-fit: contain;
  align-self: start;
  display: block;
}

.temper {
  font-style: italic;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

